  background-color: #ffffff;
  font-family: "Arial", sans-serif;
  font-size: 14px;
  line-height: 18px;
  color: #444444;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.title-line {
  position: relative;
  max-width: 230px;
}
.title-line::after {
  position: absolute;
  content: "";
  width: 1000%;
  height: 2px;
  background-color: #68b738;
  top: 50%;
  left: calc(100% + 22px);
  z-index: 100;
}
@media (min-width: 768px) {
  .title-line::after {
    left: calc(100% + 36px);
  }
}
@media (min-width: 768px) {
  .title-line {
    max-width: 350px;
  }
  .title-line::before {
    position: absolute;
    content: "";
    width: 1000%;
    height: 2px;
    background-color: #68b738;
    top: 50%;
    right: calc(100% + 36px);
  }
}

.title-line--owner-info {
  max-width: 190px;
}
@media (min-width: 768px) {
  .title-line--owner-info {
