.advantages__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  counter-reset: advantages-counter;
}
@media (min-width: 768px) {
  .advantages__list {
    display: grid;
    grid-template-columns: 1fr 1fr;
    row-gap: 37px;
    column-gap: 59px;
  }
}
@media (min-width: 1440px) {
  .advantages__list {
    display: grid;
    grid-template-columns: repeat(4, 250px);
    column-gap: 76px;
  }
}

.advantages__item {
  position: relative;
  counter-increment: advantages-counter;
}
.advantages__item::before {
  position: absolute;
  top: -2px;
  left: 0;
  content: "";
  width: 60px;
  height: 60px;
  background-color: #68b738;
}
@media (min-width: 768px) {
  .advantages__item::before {
    top: 40px;
    width: 80px;
    height: 80px;
  }
}
@media (min-width: 1440px) {
  .advantages__item::before {
    top: 37px;
    left: -2px;
  }
}
@media (min-width: 768px) {
  .advantages__item::after {
    position: absolute;
    z-index: -1;
    top: 0;
    right: 91px;
    width: 120px;
    content: counter(advantages-counter);
    font-family: "Oswald", sans-serif;
    font-size: 280px;
    line-height: 280px;
    color: #f2f2f2;
  }
}
@media (min-width: 1440px) {
  .advantages__item::after {
    top: 0;
    right: 15px;
  }
}

.advantages__item--healthy::before {
  background-image: url("../../img/index/bg-icons/healthy-icon.svg");
  background-repeat: no-repeat;
  background-position: 50% 50%;
}

.advantages__item--powder::before {
